	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"victory-guides\"><a href=\"#victory-guides\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Victory Guides</h1>\n<p>Ready to learn more about Victory? The following guides offer detailed explanations and examples that will help you get the most out of Victory. These guides assume some familiarity with the library. If you’re new to Victory, you may want to start with <a href=\"https://formidable.com/open-source/victory/docs\">our tutorial</a>.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/custom-charts\">Custom Charts</a></strong>:\nCreate custom charts that integrate seamlessly with your project. This guide explains how to compose Victory components without the <code class=\"language-text\">VictoryChart</code> wrapper, customize axes, and use functional styles to create a cohesive chart with recognizable branding.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/custom-components\">Custom Components</a></strong>:\nTake customization to the next level. This guide explains how to wrap Victory components and how to alter or create your own <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives\">primitive components</a>.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/data-accessors\">Data Accessors</a></strong>:\nMake Victory work with your data. Learn how to use data accessors to format data in Victory.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/events\">Events</a></strong>:\nCreate custom behaviors. This guide explains how to use Victory’s flexible event system to make interactive charts.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/animations\">Animations</a></strong>:\nAnimate charts. This explains how to define custom animations and transitions.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/layout\">Layout</a></strong>:\nLearn about responsive containers and VictoryPortal.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/brush-and-zoom\">Brush and Zoom</a></strong>:\nAdd interactive brushing and zooming to your charts. This guide explains how to use the <code class=\"language-text\">VictoryZoomContainer</code> and <code class=\"language-text\">VictoryBrushContainer</code></p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/themes\">Themes</a></strong>:\nApply consistent styles across all your components. This guide explains how to create your own themes, and includes a playground where you can test your own themes.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/tooltips\">Tooltips</a></strong>:\nAdd interactivity with tooltips. This guide explains how to create and customize tooltips. Topics include simple tooltips, voronoi tooltips, and integrating tooltips with custom events.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/polar-charts\">Polar Charts</a></strong>:\nCreate polar charts and configure polar axes.</p>\n<p><strong><a href=\"https://formidable.com/open-source/victory/guides/zoom-large-data\">Zoom on Large Datasets</a></strong>:\nUse <code class=\"language-text\">VictoryZoomContainer</code> to render large data sets with Victory without compromising performance.</p>","frontmatter":{"id":0,"scope":null,"title":"Victory Guides"},"fields":{"slug":"/guides/"}}},"pathContext":{"slug":"/guides/"}}

